<template>
  <div class="yourClass">
    <audio-enter :is-show="isShow" @closeAudio="closeAudio" />
  </div>
</template>
<script>
import audioEnter from './audio.vue'
export default {
  components: { audioEnter },
  data() {
    return {
      isShow: true
    }
  },
  methods: {
    closeAudio(data) {
      this.isShow = false
      alert(data)
      // 处理其他逻辑......
      this.uploadWAVData(data)
    },
    uploadWAVData(wavBlob) {
      // 创建一个formData对象
      const formData = new FormData()
      // 此处获取到blob对象后需要设置fileName满足当前项目上传需求，其它项目可直接传把blob作为file塞入formData
      const newbolb = new Blob([wavBlob], { type: 'audio/wav' })
      // 获取当时时间戳作为文件名
      const fileOfBlob = new File([newbolb], new Date().getTime() + '.wav')
      formData.append('file', fileOfBlob)
      // 使用axios发送请求
      this.$postFile('/visitor/audio/upload', formData)
        .then(response => {
          console.log('上传成功', response)
        })
        .catch(error => {
          console.error('上传失败', error)
        })
    }
  }
}
</script>
